.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100vh;
  margin: 0;
  background-color: #f4f4f4;
}

.board-container {
  display: flex;
  align-items: flex-start;
}

.board {
  display: grid;
  grid-template-columns: repeat(15, 30px);
  grid-template-rows: repeat(15, 30px);
  gap: 1px;
  margin-right: 20px;
  border: 5px solid #000; /* 增加边框宽度 */
  background-color: #fff;
  border-radius: 10px;
}

.cell {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border: 1px solid #201f1f;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.cell.black, .cell.white {
  border-radius: 50%; /* 棋子为圆形 */
  width: 28px; /* 确保圆形棋子不会超出方形格子 */
  height: 28px;
}

.cell.black {
  background-color: rgb(142, 142, 241); /* 黑色棋子颜色 */
}

.cell.white {
  background-color: rgb(98, 222, 247); /* 白色棋子颜色 */
}

button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #95eef9;
  margin-top: 20px;
}

button:hover {
  background-color: #4dcbd9;
}

h3 {
  margin-top: 30px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin-top: 10px;
}

ul li {
  margin: 5px 0;
}

ul button {
  font-size: 14px;
}

.history-container {
  width: 200px;
  max-height: 400px;
  overflow-y: auto;
}

.current-player {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center;
}
.reset-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 5px;
  background-color: #aafdeb;
  color: white;
  margin-top: 20px;
  display: block;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}

.reset-button:hover {
  background-color: #75fee5;
}